 
  body {
   
    margin: 0 auto ;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
   
    background-image: url("https://awv3node-homepage.surge.sh/build/assets/stars.svg");
     background-color: #282c34;
  }

  .todo-title {
    cursor: pointer;
  }

  .completed-todo {
    text-decoration: line-through;
  }

  .tab-list>span {
    padding: 5px 8px;
    border: 1px solid #282c34;
    border-radius: 10px;
    margin-right: 5px;
    cursor: pointer;
  }

  .tab-list>span.active {
    background-color: #282c34;
    color: #ffffff;
  }


  .container > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  pointer-events: none;
  /* justify-content: start !important; */
  font-family: 'Kanit', sans-serif;
  line-height: 2px;
  text-transform: uppercase;
}

.number {
  font-size: 450px;
  color: #373c4c;
}

.number span {
  display: inline-block;
  position: relative;
  transform: translate3d(-35%, 0, 0);
}

.header {
  margin-left: 350px;
  font-size: 70px;
  color: white;
}

.socialIcon {
  font-size: 30;
  align-content: center;

}
.socialDiv {
  margin-Top:20;
  margin-right: 10px;
  align-content: center;
  border-radius: 25px;
  background: #7dcfe486;
  padding: 20px;
  width: 7.2vh;
  height: 7vh;
  border: 1px solid #BFBFBF;
  box-shadow: 5px 5px 5px #f1f6ff18;
}
.stripe {
  height: 2px;
  width: auto;
}

.slopeBegin {
  background-color: #20232f;
  clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
}

.slopeEnd {
  clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
}

.slopeBegin,
.slopeEnd {
  position: absolute;
  width: 140%;
  height: 100%;
  cursor: pointer;
}

.pink {
  background: linear-gradient(to right, deeppink 0%, coral 100%);
}

.teal {
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
}

.tomato {
  background: linear-gradient(to right, tomato 0%, gold 100%);
}
